// Helper function to convert month number to name
const getMonthName = (month: number): string => {
  const months = [
    'Jan', 'Feb', 'Mar', 'Apr', 'May', 'Jun',
    'Jul', 'Aug', 'Sep', 'Oct', 'Nov', 'Dec'
  ];
  return months[month - 1]; // Adjust for 0-indexed array
};

// Update the TimelineItemComponent to show initials for freelance work
const TimelineItemComponent: React.FC<TimelineItemProps> = ({ job }) => {
  // Function to check if this is the freelance entry
  const isFreelance = job.company === "Freelance / Contract / Self-Employed";
  
  // Calculate the duration text
  const getDurationText = (): string => {
    const startDate = `${getMonthName(job.startMonth)} ${job.startYear}`;
    const endDate = job.endYear 
      ? `${getMonthName(job.endMonth!)} ${job.endYear}` 
      : 'Present';
    const duration = calculateDuration(job.startYear, job.startMonth, job.endYear, job.endMonth);
    
    return `${startDate} - ${endDate}  ${duration}`;
  };

  return (
    <TimelineItemWrapper>
      <TimelineDate>{getDurationText()}</TimelineDate>
      <TimelineContent>
        <TimelineHeader>
          {isFreelance ? (
            <InitialsLogo>SH</InitialsLogo>
          ) : (
            <CompanyLogo>
              <img src={companyLogos[job.company]} alt={`${job.company} logo`} />
            </CompanyLogo>
          )}
          <TimelineTitles>
            <TimelineTitle>{job.position}</TimelineTitle>
            <TimelineSubtitle>
              {job.website ? (
                <TitleLink href={job.website} target="_blank" rel="noopener noreferrer">
                  {job.company} <LinkIcon />
                </TitleLink>
              ) : (
                job.company
              )}
              {job.location && <LocationText>{job.location}</LocationText>}
            </TimelineSubtitle>
          </TimelineTitles>
        </TimelineHeader>
        <TimelineDescription>
          <BulletList>
            {job.description.map((bullet, idx) => (
              <BulletItem 
                key={idx} 
                dangerouslySetInnerHTML={{ __html: parseBoldText(bullet) }}
              />
            ))}
          </BulletList>
        </TimelineDescription>
        <TechStack>
          {job.technologies.map((tech, idx) => (
            <TechTag key={idx}>{tech}</TechTag>
          ))}
        </TechStack>
      </TimelineContent>
    </TimelineItemWrapper>
  );
};

