const ProjectsPage: React.FC = () => {
  const professionalProjects = projectsData.filter(project => project.category === 'professional');
  const freelanceProjects = projectsData.filter(project => project.category === 'freelance');

  const containerVariants = {
    hidden: { opacity: 0 },
    visible: {
      opacity: 1,
      transition: {
        staggerChildren: 0.2,
      },
    },
  };

  const itemVariants = {
    hidden: { opacity: 0, y: 30 },
    visible: {
      opacity: 1,
      y: 0,
      transition: {
        duration: 0.4,
      },
    },
  };

  const renderProjects = (projects: Project[]) => (
    <ProjectsGrid
      as={motion.div}
      variants={containerVariants}
      initial="hidden"
      animate="visible"
    >
      {projects.map((project) => (
        <ProjectCard
          key={project.id}
          as={motion.div}
          variants={itemVariants}
        >
          <ProjectImageContainer>
            <ProjectImage src={project.image} alt={project.title} />
            <ProjectOverlay>
              <ProjectLinks>
                {project.githubLink && (
                  <ProjectLink href={project.githubLink} target="_blank" rel="noopener noreferrer" title="GitHub Repository">
                    <Icon icon={FiGithub} size={18} />
                  </ProjectLink>
                )}
                {project.liveLink && (
                  <ProjectLink href={project.liveLink} target="_blank" rel="noopener noreferrer" title="Live Website">
                    <Icon icon={FiExternalLink} size={18} />
                  </ProjectLink>
                )}
              </ProjectLinks>
            </ProjectOverlay>
          </ProjectImageContainer>
          <ProjectContent>
            <ProjectHeader>
              <ProjectTitle>{project.title}</ProjectTitle>
              <CompanyInfo>
                {project.position && project.company ? 
                  `${project.position} at ${project.company}` :
                  project.position || ''}
              </CompanyInfo>
            </ProjectHeader>
            <ProjectDescription>{project.description}</ProjectDescription>
            <TechStack>
              {project.technologies.map((tech, index) => (
                <TechTag key={index}>{tech}</TechTag>
              ))}
            </TechStack>
          </ProjectContent>
        </ProjectCard>
      ))}
    </ProjectsGrid>
  );

  return (
    <ProjectsPageContainer>
      <PageBackground variant="projects" />
      <Section
        id="projects"
        title="Projects"
        subtitle="A selection of my recent work and contributions"
      >
        <SectionDivider>
          <SectionTitle>Professional Work</SectionTitle>
          {renderProjects(professionalProjects)}
        </SectionDivider>
        
        <SectionDivider>
          <SectionTitle>Freelance Projects</SectionTitle>
          {renderProjects(freelanceProjects)}
        </SectionDivider>
      </Section>
    </ProjectsPageContainer>
  );
};

