import React, { useState, useRef, useEffect } from 'react';
import Section from '../components/Section';
import Button from '../components/Button';
import styled from 'styled-components';
import { motion } from 'framer-motion';
import { FiMail, FiPhone, FiMapPin, FiSend, FiLinkedin, FiGithub } from 'react-icons/fi';
import emailjs from 'emailjs-com';
import PageBackground from '../components/PageBackground';

const ContactPage: React.FC = () => {
  const formRef = useRef<HTMLFormElement>(null);
  const [formState, setFormState] = useState({
    from_name: '',
    reply_to: '',
    subject: '',
    message: '',
  });
  
  useEffect(() => {
    // Initialize EmailJS with your public key
    emailjs.init("mitFBwd43Ky99sQf6");
  }, []);
  
  const [formStatus, setFormStatus] = useState<'idle' | 'submitting' | 'success' | 'error'>('idle');
  
  const handleChange = (e: React.ChangeEvent<HTMLInputElement | HTMLTextAreaElement>) => {
    const { name, value } = e.target;
    setFormState((prev) => ({ ...prev, [name]: value }));
  };
  
  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault();
    
    // Validate required fields
    if (!formState.from_name || !formState.reply_to || !formState.subject || !formState.message) {
      setFormStatus('error');
      setTimeout(() => {
        setFormStatus('idle');
      }, 3000);
      return;
    }
    
    setFormStatus('submitting');
    
    const serviceId = 'service_aadmhtt';
    const templateId = 'template_bcmc59t';
    
    if (!formRef.current) return;
    
    // Use send method instead of sendForm
    // @ts-ignore
    emailjs.sendForm(serviceId,
      templateId,
      formRef.current,
      "mitFBwd43Ky99sQf6")
      .then((result) => {
        console.log('Email successfully sent!', result.text);
        setFormStatus('success');
        setFormState({
          from_name: '',
          reply_to: '',
          subject: '',
          message: '',
        });
        
        // Reset form status after 3 seconds
        setTimeout(() => {
          setFormStatus('idle');
        }, 3000);
      })
      .catch((error) => {
        console.error('Failed to send email:', error);
        setFormStatus('error');
        
        // Reset form status after 3 seconds
        setTimeout(() => {
          setFormStatus('idle');
        }, 3000);
      });
  };
  
  return (
    <ContactContainer>
      <PageBackground variant="contact" />
      <Section
        id="contact"
        title="Get In Touch"
        subtitle="Have a question or want to work together? Reach out to me!"
      >
        <ContactContent>
          <ContactInfo
            as={motion.div}
            initial={{ opacity: 0, x: -30 }}
            animate={{ opacity: 1, x: 0 }}
            transition={{ duration: 0.5 }}
          >
            <ContactHeading>Contact Information</ContactHeading>
            <ContactDescription>
              Fill out the form or get in touch directly using the information below.
            </ContactDescription>
            
            <ContactMethods>
              <ContactMethod>
                <ContactIcon>
                  <FiMail size={20} />
                </ContactIcon>
                <ContactMethodContent>
                  <ContactMethodLabel>Email</ContactMethodLabel>
                  <ContactMethodValue>
                    <ContactLink href="mailto:surhak96@gmail.com">
                      surhak96@gmail.com
                    </ContactLink>
                  </ContactMethodValue>
                </ContactMethodContent>
              </ContactMethod>
              
              <ContactMethod>
                <ContactIcon>
                  <FiPhone size={20} />
                </ContactIcon>
                <ContactMethodContent>
                  <ContactMethodLabel>Phone</ContactMethodLabel>
                  <ContactMethodValue>
                    <ContactLink href="tel:+37495222706">
                      +374 95 222 706
                    </ContactLink>
                  </ContactMethodValue>
                </ContactMethodContent>
              </ContactMethod>
              
              <ContactMethod>
                <ContactIcon>
                  <FiMapPin size={20} />
                </ContactIcon>
                <ContactMethodContent>
                  <ContactMethodLabel>Location</ContactMethodLabel>
                  <ContactMethodValue>
                    <ContactLink href="https://www.google.com/maps/place/Yerevan,+Armenia" target="_blank" rel="noopener noreferrer">
                      Yerevan, Armenia
                    </ContactLink>
                  </ContactMethodValue>
                </ContactMethodContent>
              </ContactMethod>
            </ContactMethods>
            
            <SocialLinks>
              <SocialLink href="https://linkedin.com/in/surhak" target="_blank" rel="noopener noreferrer">
                <FiLinkedin size={20} />
              </SocialLink>
              <SocialLink href="https://github.com/surohak" target="_blank" rel="noopener noreferrer">
                <FiGithub size={20} />
              </SocialLink>
            </SocialLinks>
          </ContactInfo>
          
          <ContactForm
            ref={formRef}
            as={motion.form}
            initial={{ opacity: 0, x: 30 }}
            animate={{ opacity: 1, x: 0 }}
            transition={{ duration: 0.5 }}
            onSubmit={handleSubmit}
          >
            <FormRow>
              <FormGroup>
                <FormLabel htmlFor="from_name">Name</FormLabel>
                <FormInput
                  type="text"
                  id="from_name"
                  name="from_name"
                  value={formState.from_name}
                  onChange={handleChange}
                  required
                  placeholder="Your Name"
                />
              </FormGroup>
              
              <FormGroup>
                <FormLabel htmlFor="reply_to">Email</FormLabel>
                <FormInput
                  type="email"
                  id="reply_to"
                  name="reply_to"
                  value={formState.reply_to}
                  onChange={handleChange}
                  required
                  placeholder="Your Email"
                />
              </FormGroup>
            </FormRow>
            
            <FormGroup>
              <FormLabel htmlFor="subject">Subject</FormLabel>
              <FormInput
                type="text"
                id="subject"
                name="subject"
                value={formState.subject}
                onChange={handleChange}
                required
                placeholder="Subject"
              />
            </FormGroup>
            
            <FormGroup>
              <FormLabel htmlFor="message">Message</FormLabel>
              <FormTextarea
                id="message"
                name="message"
                value={formState.message}
                onChange={handleChange}
                required
                placeholder="Your Message"
                rows={5}
              />
            </FormGroup>
            
            <FormButton>
              <Button
                type="submit"
                variant="primary"
                size="large"
                icon={<FiSend />}
                disabled={formStatus === 'submitting'}
                fullWidth
              >
                {formStatus === 'submitting' ? 'Sending...' : 'Send Message'}
              </Button>
            </FormButton>
            
            {formStatus === 'success' && (
              <SuccessMessage
                as={motion.div}
                initial={{ opacity: 0, y: 10 }}
                animate={{ opacity: 1, y: 0 }}
                exit={{ opacity: 0, y: 10 }}
              >
                Your message has been sent successfully!
              </SuccessMessage>
            )}
            
            {formStatus === 'error' && (
              <ErrorMessage
                as={motion.div}
                initial={{ opacity: 0, y: 10 }}
                animate={{ opacity: 1, y: 0 }}
                exit={{ opacity: 0, y: 10 }}
              >
                There was an error sending your message. Please try again.
              </ErrorMessage>
            )}
          </ContactForm>
        </ContactContent>
      </Section>
    </ContactContainer>
  );
};

const ContactContainer = styled.div`
  padding-top: 80px;
`;

const ContactContent = styled.div`
  display: grid;
  grid-template-columns: 1fr 2fr;
  gap: 3rem;
  
  @media (max-width: 992px) {
    grid-template-columns: 1fr;
  }
`;

const ContactInfo = styled.div`
  background-color: var(--bg-secondary);
  border-radius: 12px;
  padding: 2rem;
  display: flex;
  flex-direction: column;
  height: fit-content;
  box-shadow: 0 5px 15px var(--shadow);
`;

const ContactHeading = styled.h3`
  font-size: 1.5rem;
  margin-bottom: 1rem;
  color: var(--text-primary);
`;

const ContactDescription = styled.p`
  font-size: 1rem;
  line-height: 1.6;
  color: var(--text-secondary);
  margin-bottom: 2rem;
`;

const ContactMethods = styled.div`
  display: flex;
  flex-direction: column;
  gap: 1.5rem;
  margin-bottom: 2rem;
`;

const ContactMethod = styled.div`
  display: flex;
  align-items: flex-start;
  gap: 1rem;
`;

const ContactIcon = styled.div`
  width: 40px;
  height: 40px;
  border-radius: 50%;
  background-color: var(--bg-primary);
  color: var(--accent);
  display: flex;
  align-items: center;
  justify-content: center;
  flex-shrink: 0;
`;

const ContactMethodContent = styled.div`
  display: flex;
  flex-direction: column;
`;

const ContactMethodLabel = styled.span`
  font-size: 0.9rem;
  color: var(--text-secondary);
  margin-bottom: 0.25rem;
`;

const ContactMethodValue = styled.span`
  font-size: 1rem;
  color: var(--text-primary);
`;

const ContactLink = styled.a`
  color: var(--text-primary);
  text-decoration: none;
  transition: color 0.3s ease;
  
  &:hover {
    color: var(--accent);
  }
`;

const SocialLinks = styled.div`
  display: flex;
  gap: 1rem;
  margin-top: auto;
`;

const SocialLink = styled.a`
  width: 40px;
  height: 40px;
  border-radius: 50%;
  background-color: var(--bg-primary);
  color: var(--text-primary);
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 0.3s ease;
  
  &:hover {
    background-color: var(--accent);
    color: white;
    transform: translateY(-3px);
  }
`;

const ContactForm = styled.form`
  background-color: var(--bg-secondary);
  border-radius: 12px;
  padding: 2rem;
  box-shadow: 0 5px 15px var(--shadow);
`;

const FormRow = styled.div`
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 1rem;
  
  @media (max-width: 576px) {
    grid-template-columns: 1fr;
  }
`;

const FormGroup = styled.div`
  margin-bottom: 1.5rem;
`;

const FormLabel = styled.label`
  display: block;
  font-size: 1rem;
  font-weight: 500;
  margin-bottom: 0.5rem;
  color: var(--text-primary);
`;

const inputStyles = `
  width: 100%;
  padding: 0.75rem 1rem;
  font-size: 1rem;
  background-color: var(--bg-primary);
  color: var(--text-primary);
  border: 1px solid var(--border);
  border-radius: 8px;
  transition: border-color 0.3s ease, box-shadow 0.3s ease;
  
  &:focus {
    outline: none;
    border-color: var(--accent);
    box-shadow: 0 0 0 3px rgba(0, 102, 255, 0.1);
  }
  
  &::placeholder {
    color: var(--text-secondary);
    opacity: 0.7;
  }
`;

const FormInput = styled.input`
  ${inputStyles}
`;

const FormTextarea = styled.textarea`
  ${inputStyles}
  resize: vertical;
  min-height: 120px;
`;

const FormButton = styled.div`
  margin-top: 1rem;
`;

const StatusMessage = styled(motion.div)`
  padding: 1rem;
  border-radius: 8px;
  margin-top: 1rem;
  font-size: 0.9rem;
  text-align: center;
`;

const SuccessMessage = styled(StatusMessage)`
  background-color: rgba(76, 175, 80, 0.1);
  color: #4caf50;
  border: 1px solid #4caf50;
`;

const ErrorMessage = styled(StatusMessage)`
  background-color: rgba(244, 67, 54, 0.1);
  color: #f44336;
  border: 1px solid #f44336;
`;

export default ContactPage; 