import React, { useEffect, useState } from 'react';
import styled from 'styled-components';
import { motion, useTransform, useScroll } from 'framer-motion';

interface PageBackgroundProps {
  variant?: 'about' | 'projects' | 'skills' | 'contact';
}

const PageBackground: React.FC<PageBackgroundProps> = ({ variant = 'about' }) => {
  const [windowHeight, setWindowHeight] = useState(0);
  const { scrollY } = useScroll();
  
  // Set the window height on mount for scroll calculations
  useEffect(() => {
    setWindowHeight(window.innerHeight);
    
    const handleResize = () => {
      setWindowHeight(window.innerHeight);
    };
    
    window.addEventListener('resize', handleResize);
    return () => window.removeEventListener('resize', handleResize);
  }, []);
  
  // Create scroll-driven transforms for the gradient blobs
  const rotateBlob1 = useTransform(scrollY, [0, windowHeight * 2], [0, 360]);
  const translateXBlob1 = useTransform(scrollY, [0, windowHeight], [0, 50]);
  const translateYBlob1 = useTransform(scrollY, [0, windowHeight], [0, -30]);
  const scaleBlob1 = useTransform(scrollY, [0, windowHeight * 1.5], [1, 1.2]);
  
  const rotateBlob2 = useTransform(scrollY, [0, windowHeight * 2], [0, -360]);
  const translateXBlob2 = useTransform(scrollY, [0, windowHeight], [0, -40]);
  const translateYBlob2 = useTransform(scrollY, [0, windowHeight], [0, 20]);
  const scaleBlob2 = useTransform(scrollY, [0, windowHeight * 1.5], [1, 0.8]);
  
  const gridOpacity = useTransform(scrollY, [0, windowHeight], [0.015, 0.025]);
  
  // Define different colors for different page types
  const getColor = () => {
    switch (variant) {
      case 'about':
        return 'var(--accent)';
      case 'projects':
        return '#6366f1'; // Indigo
      case 'skills':
        return '#10b981'; // Emerald
      case 'contact':
        return '#f59e0b'; // Amber
      default:
        return 'var(--accent)';
    }
  };

  return (
    <AnimationContainer>
      <GradientBlob 
        style={{ 
          rotate: rotateBlob1,
          x: translateXBlob1,
          y: translateYBlob1,
          scale: scaleBlob1,
        }}
        animate={{
          filter: ["blur(100px)", "blur(120px)", "blur(90px)"],
        }}
        transition={{
          filter: {
            duration: 5,
            repeat: Infinity,
            repeatType: 'reverse',
          }
        }}
        blurRadius={100}
        color={getColor()}
        opacity={0.04}
        size={500}
        top="5%"
        right="10%"
      />
      <GradientBlob 
        style={{ 
          rotate: rotateBlob2,
          x: translateXBlob2,
          y: translateYBlob2,
          scale: scaleBlob2,
        }}
        animate={{
          filter: ["blur(120px)", "blur(100px)", "blur(140px)"],
        }}
        transition={{
          filter: {
            duration: 8,
            repeat: Infinity,
            repeatType: 'reverse',
          }
        }}
        blurRadius={120}
        color={getColor()}
        opacity={0.03}
        size={600}
        bottom="10%"
        left="5%"
      />
      <GridPattern style={{ opacity: gridOpacity }} />
    </AnimationContainer>
  );
};

const AnimationContainer = styled.div`
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  overflow: hidden;
  z-index: 0;
  pointer-events: none;
`;

interface GradientBlobProps {
  blurRadius: number;
  color: string;
  opacity: number;
  size: number;
  top?: string;
  left?: string;
  right?: string;
  bottom?: string;
}

const GradientBlob = styled(motion.div)<GradientBlobProps>`
  position: absolute;
  width: ${props => props.size}px;
  height: ${props => props.size}px;
  border-radius: 50%;
  background-color: ${props => props.color};
  opacity: ${props => props.opacity};
  filter: blur(${props => props.blurRadius}px);
  top: ${props => props.top || 'auto'};
  left: ${props => props.left || 'auto'};
  right: ${props => props.right || 'auto'};
  bottom: ${props => props.bottom || 'auto'};
  z-index: 0;
`;

const GridPattern = styled(motion.div)`
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-image: linear-gradient(var(--border) 1px, transparent 1px),
    linear-gradient(90deg, var(--border) 1px, transparent 1px);
  background-size: 50px 50px;
  opacity: 0.015;
  z-index: 1;
`;

export default PageBackground; 